import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';
import { FichaModeloService } from 'src/app/services/fichas/ficha-modelo.service';

import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';

@Component({
  selector: 'app-ficha-modelo-detail',
  templateUrl: './ficha-modelo-detail.component.html',
  styleUrls: ['./ficha-modelo-detail.component.scss'],
})
export class FichaModeloDetailComponent implements OnInit {

  fichaModelo: any;
  loading = true;

  constructor(
    private modal: ModalController,
    private modalInterno: ModalController,
    private detalharService: DetalharService,
    private fmService: FichaModeloService,
    private toast: MessageService,
  ) {

  }

  ngOnInit() {
    const id = this.detalharService.getId();
    this.fmService.findFichaModeloById(id)
      .subscribe(res => {
        console.log(res);
        this.fichaModelo = res;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }


  close() {
    this.modal.dismiss(null, null, "capitulo-detail");
  }

  mudaAba(event) {
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  getAtributos() {
    console.log(this.fichaModelo.atributos);
    this.fichaModelo.atributos;
  }

  getKarma() {
    const { karmaAtual, karmaTotal } = this.fichaModelo;

    if (karmaAtual || karmaTotal)
      return `${karmaAtual}/${karmaTotal}`;
    else
      return `Desconhecido`;
  }

  getPV() {
    const { pvAtual, pvTotal } = this.fichaModelo;

    if (pvAtual || pvTotal)
      return `${pvAtual}/${pvTotal}`;
    else
      return `Desconhecido`;
  }

  getPM() {
    const { pmAtual, pmTotal } = this.fichaModelo;

    if (pmAtual || pmTotal)
      return `${pmAtual}/${pmTotal}`;
    else
      return `Desconhecido`;
  }

}
